/* General body styles */
body {
  margin: 0;
  padding: 0;
  font-family: 'Arial', sans-serif;
  background-color: #fff;
}

/* Main background */
.background {
  background-image: url('../public/IMG3.jpeg'); /* New background image */
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 20px;
  box-sizing: border-box;
}

/* Container for the movie list */
.movie-list-container {
  background: rgba(255, 255, 255, 0.85); /* Transparent background */
  border: 2px solid #e2aaff; /* Pinkish border to match theme */
  border-radius: 15px;
  padding: 20px;
  width: 90%; /* Adjust for responsive design */
  max-width: 800px; /* Increased max width */
  text-align: left;
  font-size: 0.8em;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.25);
  box-sizing: border-box;
}

