/* Title */
.title {
  font-size: 1.4em;
  margin-bottom: 15px;
  color: #d55b5b; /* Festive red-pink */
  font-weight: bold;
  text-align: center;
}

.title1 {
  font-size: 1.8em;
  margin-bottom: 10px;
  color: #d55b5b; /* Festive red-pink */
  font-weight: bold;
  text-align: center;
}


/* Movie list styles */
.movie-list-columns {
  display: grid;
  grid-template-columns: repeat(2, 1fr); /* Two columns */
  gap: 10px; /* Spacing between items */
}

.custom-checkbox {
  display: flex;
  align-items: center;
  cursor: pointer;
  font-size: 1em;
  color: #333;
  justify-content: flex-start;
}

.custom-checkbox input {
  display: none; /* Hide default checkbox */
}

.custom-checkbox .checkmark {
  width: 14px;
  min-width: 14px;
  height: 14px;
  border: 2px solid #d55b5b; /* Border color */
  border-radius: 4px;
  margin-right: 10px;
  position: relative;
  display: inline-block;
  background: white;
  transition: background 0.3s ease, border-color 0.3s ease;
}

.custom-checkbox input:checked + .checkmark {
  background: #d55b5b; /* Fill color when checked */
  border-color: #d55b5b;
}

.custom-checkbox .checkmark::after {
  content: '';
  position: absolute;
  width: 6px;
  height: 10px;
  border: solid white;
  border-width: 0 2px 2px 0;
  transform: rotate(45deg);
  top: 0px;
  left: 3px;
  display: none;
}

.custom-checkbox input:checked + .checkmark::after {
  display: block;
}

/* Save button styles */
.save-button-div {
  display: flex;
  align-items: center;
  justify-content: center;
}

.save-button {
  margin-top: 20px;
  padding: 12px 24px;
  font-size: 1em;
  font-weight: bold;
  color: white;
  background-color: #d55b5b;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
  transition: background 0.3s ease;
}

.save-button:hover {
  background-color: #b04848;
}

/* Responsive styles */
@media (max-width: 480px) {


  .title {
    font-size: 1.5em;
  }

  .custom-checkbox {
    font-size: 0.9em;
  }

  .save-button {
    padding: 10px 18px;
    font-size: 0.9em;
  }
}
